anera efectiva en contextos reales. Este modelo exige colocar al estudiante en el centro del proceso de aprendizaje, otorgándole un papel activo en la construcción de sus propias competencias. Los objetivos del currículo se definen en términos de competencias y capacidades, abarcando el desarrollo de habilidades cognitivas, motrices e interpersonales. Las competencias se entienden como comportamientos y actitudes transversales a diferentes ámbitos de la actividad personal y laboral, integrando conocimientos (\"saber\"), habilidades (\"saber hacer\") y actitudes (\"saber ser\"). El diseño curricular basado en competencias se centra en el perfil profesional de egreso, recogiendo las necesidades y realidades del ámbito internacional y nacional en diversos campos. Un enfoque de diseño curricular basado en competencias considera tanto las competencias previas de los estudiantes como las competencias que se espera que adquieran al finalizar el programa.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Modelo basado en resultados de aprendizaje"}},[_c('p',[_vm._v("Este modelo se centra en definir claramente lo que los estudiantes deben saber y ser capaces de hacer al finalizar un curso o programa. Para Bobbit, el currículo se presenta como una descripción de objetivos a lograr a través de procedimientos específicos, y Taylor enfatiza que el currículo debe responder a los fines, a los logros de experiencias, a los modos de alcanzar esos fines y a las maneras de comprobarlo. Los resultados de aprendizaje deben enunciarse de manera clara y precisa, mostrando conductas observables."),_c('br'),_c('br'),_vm._v("En este modelo, los resultados de aprendizaje se convierten en el eje articulador, guiando la secuencia de las actividades, la selección de los recursos y las estrategias de evaluación. Se recomienda que la redacción de los logros de aprendizaje se oriente hacia el logro de procesos cognitivos complejos y sea específica en su formulación. Los logros o resultados de aprendizaje están orientados a un fin, centrados en el estudiante, enfatizando su éxito presente y futuro, y demandando objetivos claros, explícitos y significativos, incorporando retroalimentación continua para la evaluación y la revisión.")])])])],1),_vm._m(1)]),_c('p',{staticClass:"mb-4"},[_vm._v("En la siguiente tabla se presenta una comparación de las características principales, ventajas y desventajas de los modelos de diseño curricular basados en competencias y por resultados de aprendizaje, en el contexto de los entornos virtuales:")]),_vm._m(2),_vm._m(3),_vm._m(4),_c('ImagenInfografica',{staticClass:"color-secundario mb-5 bg-white",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-42.png"),"alt":"Descripción de la imagen"}})])]},proxy:true}])},[_c('div',{staticClass:"tarjeta bg-white p-4",attrs:{"x":"33.9%","y":"25%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Modelo basado en competencias")]),_c('p',[_vm._v("Se enfoca en habilidades aplicadas a contextos reales y articula necesidades del entorno profesional."),_c('br'),_c('br'),_vm._v("Útil para formar profesionales adaptables y competentes.")])]),_c('div',{staticClass:"tarjeta bg-white p-4",attrs:{"x":"66%","y":"32%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Modelo basado en resultados")]),_c('p',[_vm._v("Define claramente los saberes y habilidades observables para asegurar el logro del aprendizaje."),_c('br'),_c('br'),_vm._v("Clave en sistemas de educación por logros.")])]),_c('div',{staticClass:"tarjeta bg-white p-4",attrs:{"x":"64%","y":"73.2%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Modelo comunicativo-interactivo")]),_c('p',[_vm._v("Centrado en el estudiante activo, promueve la reflexión y colaboración en entornos dinámicos."),_c('br'),_c('br'),_vm._v("Perfecto para entornos virtuales dinámicos y participativos.")])]),_c('div',{staticClass:"tarjeta bg-white p-4",attrs:{"x":"36%","y